@media (max-width: 767px) {
  .presentation__wrapper::before {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 320px;
    height: 302px;
    background-image: url("../img/index-background-mobile.png");
    background-repeat: no-repeat;
  }
  .presentation__wrapper::after {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 320px;
    height: 302px;
    background-color: #68B738;
    opacity: 85%;
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi) {
  .presentation__wrapper::before {
    background-image: url("../img/index-background-mobile@2x.png");
