  .presentation {
    position: relative;
  }
  .presentation::after {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    left: 50%;
    width: calc(100% / 2);
    height: 696px;
    background-color: #68B738;
    opacity: 85%;
  }
  .presentation::before {
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    width: 1040px;
    height: 696px;
    background-image: url("../img/index-background-desktop.png");
